<!-- Desktop only, ATM -->

<template>
    <div class="editor">
        
        <div id="pages">
            <div :class="{ active: page === 0 }" @click="page = 0">
                <img src="@/assets/items.svg">
            </div>
            <div :class="{ active: page === 1 }" @click="page = 1">
                <img src="@/assets/filter.svg">
            </div>
        </div>
        
        <div id="canvas-container">
            
            <canvas id="editor-canvas">
                
            </canvas>
            
            <div id="editor-panel">
            
            </div>
            
        </div>
    </div>
</template>

<script>

export default {
    name: 'Editor',
    
    mounted() {
        const rect = document.getElementById('canvas-container').getBoundingClientRect();
        
        const canvas = document.getElementById('editor-canvas');
        const ctx = canvas.getContext('2d');
        
        canvas.width = rect.width;
        canvas.height = rect.height;
        
        const toteImg = this.loadImg('tote.png', () => {
            const aspect = toteImg.height / toteImg.width;
            
            ctx.drawImage(toteImg, 420, -200, 750, 750 * aspect);
            ctx.fillStyle = '#000';
            
            ctx.rect(440, 170, 700, 700);
            ctx.stroke();
        });
    },
    
    data() {
        return {
            page: 0
        }
    },
    
    methods: {
        loadImg(path, onload) {
            const img = document.createElement('img');
            img.src = path;
            
            img.addEventListener('load', () => {
                onload();
                img.remove();
                
                /* Remove the event listener ? */
            });
            
            return img;
        }
    }
}
</script>

<style>

#pages
{       
    position: absolute;
    
    padding: 8px;
    display: flex;
    flex-direction: row;
    gap: 8px;
    
    background-color: var(--main-color);
}

#pages > div
{
    box-sizing: content-box;
    
    transition: background-color .2s ease;
}

#pages > div.active
{
    background-color: var(--selected-color);
}

#pages > div > img
{
    height: 24px;
    user-select: none;
}



#canvas-container
{
    margin-top: -16px;
    
    width: 100%;
    height: calc(100vh - 64px); /* - 64px for the navbar */
    overflow: hidden;
}

#editor-canvas
{
}

#editor-panel
{
    position: absolute;
    
    top: -16px;
    right: 0;
    
    width: 250px;
    height: calc(100% + 16px);
    
    background-color: var(--main-color);
}

</style>